body {
	background-color: #1e1e1e;
	color: #d4d4d4;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
		'Open Sans', 'Helvetica Neue', sans-serif;
	margin: 0;
	padding: 0;
	animation: fadeIn ease 1s;
	animation-iteration-count: 1;
	animation-fill-mode: forwards;
}

@keyframes fadeIn {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

.header .container {
	display: flex;
	flex-wrap: nowrap;
	align-items: center;
}

.container {
	max-width: 960px;
	margin: 0 auto;
	padding: 20px;
	box-sizing: border-box;
}

.header {
	background-color: #333;
	padding: 10px 0;
	animation: slideDown ease 1s;
	animation-iteration-count: 1;
	animation-fill-mode: forwards;
}

.header h1 {
	margin-top: 0px;
	margin-bottom: 0px;
	font-size: 3rem;
}

.profile-picture {
	width: 150px;
	height: 150px;
	overflow: hidden;
	border-radius: 50%;
	margin-right: 20px;
}

.round-image {
	width: 100%;
	height: auto;
}

@keyframes slideDown {
	0% {
		transform: translateY(-50px);
		opacity: 0;
	}
	100% {
		transform: translateY(0);
		opacity: 1;
	}
}

.navbar {
	display: flex;
	justify-content: center;
	flex-direction: row !important;
	margin-top: 20px;
	animation: slideUp ease 1s;
	animation-iteration-count: 1;
	animation-fill-mode: forwards;
}

@keyframes slideUp {
	0% {
		transform: translateY(50px);
		opacity: 0;
	}
	100% {
		transform: translateY(0);
		opacity: 1;
	}
}

.navbar a, #contact a, .project-reference {
	color: #61dafb;
	text-decoration: none;
}

.navbar a {
	margin: 0 15px;
	font-size: 18px;
}

.navbar a:hover {
	transition: all 0.3s ease;
	transform: scale(1.3);
}

#contact a:hover, .project-reference:hover {
	text-decoration: underline;
}

.project-reference-p {
	font-size: 1.4em;
}

.section {
	/* margin-top: 40px; */
	animation: fadeInUp ease 1s;
	animation-iteration-count: 1;
	animation-fill-mode: forwards;
}

.project {
	display: flex;
	grid-gap: 20px;
	border-radius: 10px;
	padding: 10px;
	background-color: #333;
}

.project-pictures {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	grid-gap: 10px;
}

.project-picture {
	width: 350px;
	border-radius: 10px;
}

.contact-logo {
	width: 15px;
	height: 15px;
}

#contact a {
	margin: 10px;
}

@keyframes fadeInUp {
	0% {
		transform: translateY(20px);
		opacity: 0;
	}
	100% {
		transform: translateY(0);
		opacity: 1;
	}
}

.footer {
	background-color: #333;
	color: #61dafb;
	text-align: center;
	padding: 10px 0;
	position: fixed;
	bottom: 0;
	width: 100%;
	animation: slideUp ease 1s;
	animation-iteration-count: 1;
	animation-fill-mode: forwards;
	position: relative;
}

.scroll-to-top {
	position: fixed;
	bottom: 20px;
	right: 20px;
	background-color: #007bff;
	color: #ffffff;
	border: none;
	border-radius: 10px;
	width: 50px;
	height: 50px;
	font-size: 20px;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	opacity: 0;
	font-size: 1.5em;
	transform: translateY(100%);
	transition: opacity 0.3s ease, transform 0.3s ease;
}

.scroll-to-top.visible {
	opacity: 1;
	transform: translateY(0);
}

.scroll-to-top:hover {
	background-color: #0056b3;
}

@media (max-width: 768px) {
	.container {
		padding: 10px;
	}

	.header .container {
		padding: 0px;
	}

	.header h1 {
		font-size: 1.5rem;
	}

	.profile-picture {
		width: 75px;
		height: 75px;
		overflow: hidden;
		border-radius: 50%;
		margin-left: 10px;
		margin-right: 10px;
	}

	.navbar {
		flex-direction: column;
		align-items: center;
		text-align: center;
	}

	.project {
		flex-direction: column;
	}

	.project-picture {
		width: 100%;
	}

	#contact {
		margin-bottom: 30px;
	}
}
